import { createRouter, createWebHistory } from 'vue-router'

// 导入页面组件
import Home from '../views/Home.vue'
import TaskList from '../views/TaskList.vue'
import HouseList from '../views/HouseList.vue'
import Analysis from '../views/Analysis.vue'
import NotFound from '../views/NotFound.vue'
// 导入验证码相关组件
import CaptchaList from '../views/captcha/CaptchaList.vue'
import CaptchaControl from '../views/captcha/CaptchaControl.vue'
import CaptchaVerify from '../views/captcha/CaptchaVerify.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home,
    meta: {
      title: '首页 - 租房数据分析系统'
    }
  },
  {
    path: '/tasks',
    name: 'taskList',
    component: TaskList,
    meta: {
      title: '爬虫任务列表 - 租房数据分析系统'
    }
  },
  {
    path: '/houses',
    name: 'houseList',
    component: HouseList,
    meta: {
      title: '房源列表 - 租房数据分析系统'
    }
  },
  {
    path: '/analysis',
    name: 'analysis',
    component: Analysis,
    meta: {
      title: '数据分析 - 租房数据分析系统'
    }
  },
  // 验证码相关路由
  {
    path: '/captcha',
    name: 'captchaList',
    component: CaptchaList,
    meta: {
      title: '验证码会话列表 - 租房数据分析系统'
    }
  },
  {
    path: '/captcha/control/:sessionId',
    name: 'captchaControl',
    component: CaptchaControl,
    meta: {
      title: '验证码控制 - 租房数据分析系统'
    }
  },
  {
    path: '/captcha/verify/:sessionId',
    name: 'captchaVerify',
    component: CaptchaVerify,
    meta: {
      title: '验证码验证 - 租房数据分析系统'
    }
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'notFound',
    component: NotFound,
    meta: {
      title: '404 Not Found - 租房数据分析系统'
    }
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

// 路由守卫，用于设置页面标题
router.beforeEach((to, from, next) => {
  // 设置页面标题
  document.title = to.meta.title || '租房数据分析系统'
  next()
})

export default router 