<template>
  <div class="crawl-task-form">
    <el-card class="task-card">
      <template #header>
        <div class="card-header">
          <h3>创建新爬虫任务</h3>
        </div>
      </template>
      
      <el-form :model="form" label-width="120px" :rules="rules" ref="taskForm">
        <el-form-item label="城市" prop="city">
          <el-select v-model="form.city" placeholder="请选择城市" style="width: 100%">
            <el-option
              v-for="(code, name) in cities"
              :key="name"
              :label="name"
              :value="name"
            />
          </el-select>
        </el-form-item>
        
        <el-form-item label="爬取页数" prop="maxPages">
          <el-input-number v-model="form.maxPages" :min="1" :max="50" style="width: 100%" />
        </el-form-item>
        
        <el-form-item>
          <el-button type="primary" @click="submitForm" :loading="loading">开始爬取</el-button>
          <el-button @click="resetForm">重置</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { ref, reactive, onMounted } from 'vue';
import { ElMessage } from 'element-plus';
import api from '../api/api';

export default {
  name: 'CrawlTaskForm',
  emits: ['task-created'],
  
  setup(props, { emit }) {
    // 表单数据
    const form = reactive({
      city: '',
      maxPages: 5
    });
    
    // 表单验证规则
    const rules = {
      city: [
        { required: true, message: '请选择城市', trigger: 'change' }
      ],
      maxPages: [
        { required: true, message: '请设置爬取页数', trigger: 'blur' }
      ]
    };
    
    // 城市列表
    const cities = ref({});
    
    // 表单引用
    const taskForm = ref(null);
    
    // 加载状态
    const loading = ref(false);
    
    // 获取城市列表
    const getCities = async () => {
      try {
        cities.value = await api.getCities();
      } catch (error) {
        ElMessage.error('获取城市列表失败');
        console.error(error);
      }
    };
    
    // 提交表单
    const submitForm = async () => {
      if (!taskForm.value) return;
      
      await taskForm.value.validate(async (valid) => {
        if (valid) {
          try {
            loading.value = true;
            
            const data = {
              city: form.city,
              max_pages: form.maxPages
            };
            
            const result = await api.createCrawlTask(data);
            
            loading.value = false;
            ElMessage.success('爬虫任务创建成功');
            
            // 通知父组件任务已创建
            emit('task-created', result);
            
            // 重置表单
            resetForm();
          } catch (error) {
            loading.value = false;
            ElMessage.error('爬虫任务创建失败');
            console.error(error);
          }
        }
      });
    };
    
    // 重置表单
    const resetForm = () => {
      if (taskForm.value) {
        taskForm.value.resetFields();
      }
    };
    
    // 组件挂载时获取城市列表
    onMounted(() => {
      getCities();
    });
    
    return {
      form,
      rules,
      cities,
      taskForm,
      loading,
      submitForm,
      resetForm
    };
  }
};
</script>

<style scoped>
.crawl-task-form {
  max-width: 600px;
  margin: 0 auto;
}

.task-card {
  margin-bottom: 20px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
</style> 