<template>
  <div class="task-list">
    <el-card>
      <template #header>
        <div class="card-header">
          <h3>爬虫任务列表</h3>
          <el-button type="primary" size="small" @click="refreshTasks">刷新</el-button>
        </div>
      </template>
      
      <el-table :data="tasks" stripe style="width: 100%" v-loading="loading">
        <el-table-column prop="id" label="ID" width="60" />
        <el-table-column prop="city" label="城市" width="100" />
        <el-table-column prop="status" label="状态" width="100">
          <template #default="scope">
            <el-tag :type="getStatusType(scope.row.status)">{{ scope.row.status }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="start_time" label="开始时间" width="180">
          <template #default="scope">
            {{ formatDate(scope.row.start_time) }}
          </template>
        </el-table-column>
        <el-table-column prop="end_time" label="结束时间" width="180">
          <template #default="scope">
            {{ scope.row.end_time ? formatDate(scope.row.end_time) : '-' }}
          </template>
        </el-table-column>
        <el-table-column prop="total_items" label="总条目" width="100" />
        <el-table-column prop="success_items" label="成功条目" width="100" />
        <el-table-column label="进度" width="180">
          <template #default="scope">
            <el-progress
              :percentage="calculateProgress(scope.row)"
              :status="getProgressStatus(scope.row.status)"
            />
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template #default="scope">
            <el-button 
              size="small"
              type="primary"
              @click="viewDetail(scope.row)"
              :disabled="scope.row.status === '进行中' || scope.row.status === 'In Progress'"
            >
              查看数据
            </el-button>
            <el-button 
              size="small"
              type="success"
              @click="runAnalysis(scope.row)"
              :disabled="scope.row.status === '进行中' || scope.row.status === 'In Progress' || scope.row.success_items === 0"
            >
              分析数据
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      
      <div class="pagination-container">
        <el-pagination
          background
          layout="total, sizes, prev, pager, next"
          :total="total"
          :page-size="pageSize"
          :current-page="currentPage"
          :page-sizes="[10, 20, 50, 100]"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </el-card>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue';
import { ElMessage } from 'element-plus';
import api from '../api/api';
import { useRouter } from 'vue-router';

export default {
  name: 'TaskList',
  
  setup() {
    const router = useRouter();
    
    // 任务列表数据
    const tasks = ref([]);
    const loading = ref(false);
    
    // 分页参数
    const currentPage = ref(1);
    const pageSize = ref(10);
    const total = ref(0);
    
    // 获取任务列表
    const fetchTasks = async () => {
      try {
        loading.value = true;
        
        const params = {
          limit: pageSize.value,
          offset: (currentPage.value - 1) * pageSize.value
        };
        
        const result = await api.getTasks(params);
        tasks.value = result;
        
        // TODO: 获取总数量，后端API需要支持
        total.value = 100; // 临时设置，实际项目中应从API获取
        
        loading.value = false;
      } catch (error) {
        loading.value = false;
        ElMessage.error('获取任务列表失败');
        console.error(error);
      }
    };
    
    // 刷新任务列表
    const refreshTasks = () => {
      fetchTasks();
    };
    
    // 处理页码变化
    const handleCurrentChange = (page) => {
      currentPage.value = page;
      fetchTasks();
    };
    
    // 处理每页显示数量变化
    const handleSizeChange = (size) => {
      pageSize.value = size;
      currentPage.value = 1;
      fetchTasks();
    };
    
    // 查看详情
    const viewDetail = (task) => {
      // 检查任务是否进行中
      if (task.status === '进行中' || task.status === 'In Progress') {
        ElMessage.warning('爬虫任务进行中，暂无法查看数据');
        return;
      }
      router.push({ name: 'houseList', query: { taskId: task.id, city: task.city } });
    };
    
    // 运行数据分析
    const runAnalysis = async (task) => {
      try {
        // 检查任务是否进行中
        if (task.status === '进行中' || task.status === 'In Progress') {
          ElMessage.warning('爬虫任务进行中，暂无法分析数据');
          return;
        }
        
        // 检查是否有成功爬取的数据
        if (task.success_items === 0) {
          ElMessage.warning('没有成功爬取的数据，无法进行分析');
          return;
        }
        
        loading.value = true;
        
        const data = {
          task_id: task.id,
          city: task.city,
          analysis_types: [] // 空数组表示运行所有类型的分析
        };
        
        await api.runAnalysis(data);
        
        loading.value = false;
        ElMessage.success('数据分析任务已启动');
        
        // 跳转到分析结果页面
        router.push({ name: 'analysis', query: { city: task.city } });
      } catch (error) {
        loading.value = false;
        ElMessage.error('启动数据分析任务失败');
        console.error(error);
      }
    };
    
    // 计算进度百分比
    const calculateProgress = (task) => {
      if (!task.total_items || task.total_items === 0) return 0;
      
      const progress = (task.success_items / task.total_items) * 100;
      return Math.min(Math.round(progress), 100);
    };
    
    // 获取状态对应的类型
    const getStatusType = (status) => {
      switch (status) {
        case '进行中':
        case 'In Progress':
          return 'warning';
        case '完成':
        case 'Completed':
          return 'success';
        case '失败':
        case 'Failed':
          return 'danger';
        default:
          return 'info';
      }
    };
    
    // 获取进度条状态
    const getProgressStatus = (status) => {
      switch (status) {
        case '进行中':
        case 'In Progress':
          return '';
        case '完成':
        case 'Completed':
          return 'success';
        case '失败':
        case 'Failed':
          return 'exception';
        default:
          return '';
      }
    };
    
    // 格式化日期
    const formatDate = (dateString) => {
      if (!dateString) return '';
      
      const date = new Date(dateString);
      return date.toLocaleString();
    };
    
    // 组件挂载时获取任务列表
    onMounted(() => {
      fetchTasks();
    });
    
    return {
      tasks,
      loading,
      currentPage,
      pageSize,
      total,
      refreshTasks,
      handleCurrentChange,
      handleSizeChange,
      viewDetail,
      runAnalysis,
      calculateProgress,
      getStatusType,
      getProgressStatus,
      formatDate
    };
  }
};
</script>

<style scoped>
.task-list {
  margin-bottom: 20px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.pagination-container {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}
</style> 