<template>
  <div class="captcha-control">
    <el-container>
      <el-header>
        <div class="header">
          <h1>验证会话控制页</h1>
          <el-button type="primary" @click="backToList">返回列表</el-button>
        </div>
      </el-header>
      
      <el-main>
        <div class="instruction-box" v-if="!sessionCompleted">
          <h3>操作指南</h3>
          <p>1. 在下方浏览器窗口中完成贝壳网的人机验证</p>
          <p>2. 点击屏幕上的元素进行操作，可以直接通过点击进行滑块验证</p>
          <p>3. 验证完成后点击"完成验证"按钮通知爬虫程序继续运行</p>
          <p>4. 如需刷新页面可以点击"刷新页面"按钮</p>
        </div>
        
        <el-alert
          v-if="sessionCompleted"
          title="验证已完成"
          type="success"
          description="此验证会话已标记为完成，爬虫程序已继续运行"
          :closable="false"
          show-icon>
        </el-alert>
        
        <div class="session-info">
          <el-descriptions title="会话信息" :column="3" border>
            <el-descriptions-item label="会话ID">{{ sessionId }}</el-descriptions-item>
            <el-descriptions-item label="状态">
              <el-tag v-if="sessionStatus === 'active'" type="success">活跃</el-tag>
              <el-tag v-else-if="sessionStatus === 'completed'" type="info">已完成</el-tag>
              <el-tag v-else-if="sessionStatus === 'closed'" type="danger">已关闭</el-tag>
              <el-tag v-else type="warning">{{ sessionStatus }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="创建时间">{{ formatTime(sessionCreatedAt) }}</el-descriptions-item>
          </el-descriptions>
        </div>
        
        <div class="browser-container" ref="browserContainer">
          <div v-if="loading" class="loading-overlay">
            <div class="el-loading-spinner">
              <svg class="circular" viewBox="25 25 50 50">
                <circle class="path" cx="50" cy="50" r="20" fill="none"/>
              </svg>
              <p class="el-loading-text">加载中...</p>
            </div>
          </div>
          
          <div class="click-indicator" v-if="showClickIndicator" :style="indicatorStyle"></div>
          
          <div v-if="!screenshot" class="no-screenshot">
            <span>加载截图中...如长时间无截图显示，请尝试点击"刷新页面"按钮</span>
          </div>
          
          <img 
            v-if="screenshot" 
            :src="screenshot" 
            alt="浏览器截图" 
            class="browser-screen"
          />
          
          <div 
            v-if="!sessionCompleted" 
            class="browser-overlay" 
            @click="handleScreenClick" 
            @mousemove="handleMouseMove"
            @mousedown="handleMouseDown"
            @mouseup="handleMouseUp"
            ref="browserOverlay">
          </div>
          
          <!-- 浮动工具面板 -->
          <div class="floating-toolbar" v-if="screenshot" 
            :style="{top: toolbarPosition.y + 'px', left: toolbarPosition.x + 'px'}" 
            v-show="isToolbarVisible">
            <div class="toolbar-button" title="点击模式" 
              :class="{ active: interactionMode === 'click' }"
              @click="setInteractionMode('click')">
              <el-icon><Pointer /></el-icon>
            </div>
            <div class="toolbar-button" title="拖动模式" 
              :class="{ active: interactionMode === 'drag' }"
              @click="setInteractionMode('drag')">
              <el-icon><DArrowRight /></el-icon>
            </div>
            <div class="toolbar-button" title="放大查看" @click="zoomIn">
              <el-icon><ZoomIn /></el-icon>
            </div>
          </div>
        </div>
        
        <!-- 智能提示区域 -->
        <div class="ai-suggestion" v-if="aiSuggestion">
          <el-icon><InfoFilled /></el-icon> <strong>智能提示:</strong> {{ aiSuggestion }}
        </div>
        
        <!-- 直接加载的图片备用显示 -->
        <!-- <div class="direct-image" v-if="!screenshot">
          <p>尝试直接加载最新截图:</p>
          <img :src="'/captcha/screenshots/session_' + sessionId + '.png?t=' + Date.now()" 
            alt="直接加载的截图" style="max-width: 100%;" />
        </div> -->
        
        <div class="action-bar">
          <el-button 
            type="primary" 
            :disabled="sessionCompleted || loading" 
            @click="completeVerification">
            完成验证
          </el-button>
          
          <el-button 
            type="success" 
            :disabled="sessionCompleted || loading" 
            @click="initDragMode">
            滑块拖动模式
          </el-button>
          
          <el-button 
            type="info" 
            :disabled="sessionCompleted || loading" 
            @click="refreshBrowser">
            刷新页面
          </el-button>
          
          <el-button 
            type="danger" 
            :disabled="sessionCompleted || loading" 
            @click="closeSession">
            关闭会话
          </el-button>
        </div>
        
        <!-- 快捷键提示面板 -->
        <div class="shortcuts-panel" v-if="showShortcuts">
          <h4>键盘快捷键 <el-button type="text" size="small" @click="showShortcuts = false">关闭</el-button></h4>
          <div><span class="shortcut-key">R</span> 刷新页面</div>
          <div><span class="shortcut-key">D</span> 滑块拖动模式</div>
          <div><span class="shortcut-key">C</span> 完成验证</div>
          <div><span class="shortcut-key">Esc</span> 取消当前模式</div>
          <div><span class="shortcut-key">F1</span> 显示/隐藏快捷键</div>
          <div><span class="shortcut-key">F2</span> 显示/隐藏调试信息</div>
        </div>
        
        <!-- 调试信息区域 -->
        <div class="debug-info" v-if="showDebug">
          <p>WebSocket状态: {{ wsStatus }}</p>
          <p>最后接收消息: {{ lastMessage }}</p>
          <p>截图更新时间: {{ lastUpdateTime }}</p>
          <el-button size="small" @click="reconnectWebSocket">重新连接WebSocket</el-button>
          <el-button size="small" @click="showDebug = false">隐藏调试信息</el-button>
        </div>
        
        <!-- 操作日志区域 -->
        <div class="operation-log">
          <h3>操作日志 <el-button type="text" @click="clearLogs">清空</el-button></h3>
          <div v-if="operationLogs.length === 0" class="log-entry log-info">
            尚无操作日志，点击截图或按钮执行操作。
          </div>
          <div v-for="(log, index) in operationLogs" :key="index" 
               :class="['log-entry', 'log-' + log.type]">
            <span>{{ log.time }} - {{ log.message }}</span>
          </div>
        </div>

        <!-- 拖动模式指示器 -->
        <div class="drag-mode-indicator" v-if="isDragMode">
          <p v-if="isDragMode === true">
            <strong>滑块拖动模式:</strong> 请点击滑块位置
          </p>
          <p v-else-if="isDragMode === 'endpoint'">
            <strong>滑块拖动模式:</strong> 请点击目标位置
          </p>
        </div>
      </el-main>
      
      <el-footer>
        <div style="text-align: center; margin-top: 20px; padding: 20px; border-top: 1px solid #eee;">
          <p>贝壳网验证码代理系统 &copy; 2025</p>
        </div>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
import { ref, reactive, computed, onMounted, onBeforeUnmount } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import axios from 'axios'
import { 
  Pointer, 
  DArrowRight, 
  ZoomIn, 
  InfoFilled
} from '@element-plus/icons-vue'
import { ElMessage } from 'element-plus'

export default {
  name: 'CaptchaControl',
  components: {
    Pointer,
    DArrowRight,
    ZoomIn,
    InfoFilled
  },
  setup() {
    // 初始化部分代码
    const router = useRouter()
    const route = useRoute()
    
    // 会话信息
    const sessionId = ref(route.params.sessionId)
    const sessionStatus = ref('')
    const sessionCreatedAt = ref('')
    const sessionCompleted = ref(false)
    
    // UI 状态
    const screenshot = ref('')
    const loading = ref(true)
    const wsConnection = ref(null)
    const browserContainer = ref(null)
    const browserOverlay = ref(null)
    
    // 拖动模式
    const isDragMode = ref(false)
    const dragStart = reactive({ x: 0, y: 0 })
    const dragEnd = reactive({ x: 0, y: 0 })
    
    // 交互模式
    const interactionMode = ref('click') // 'click', 'drag'
    
    // 工具栏状态
    const isToolbarVisible = ref(true)
    const toolbarPosition = reactive({ x: 10, y: 10 })
    
    // 鼠标状态
    const mousePosition = reactive({ x: 0, y: 0 })
    const isMouseDown = ref(false)
    
    // 智能提示
    const aiSuggestion = ref('')
    
    // 调试信息
    const wsStatus = ref('未连接')
    const lastMessage = ref('无')
    const lastUpdateTime = ref('无')
    const showDebug = ref(false)
    const showShortcuts = ref(true)
    
    // 操作日志
    const operationLogs = ref([])
    const maxLogs = 20 // 最多显示20条日志
    
    // 点击指示器
    const showClickIndicator = ref(false)
    const indicatorPosition = reactive({ x: 0, y: 0 })
    
    const indicatorStyle = computed(() => ({
      left: `${indicatorPosition.x}px`,
      top: `${indicatorPosition.y}px`
    }))
    
    // 格式化时间
    const formatTime = (timeStr) => {
      if (!timeStr) return ''
      try {
        return new Date(timeStr).toLocaleString()
      } catch (e) {
        return timeStr
      }
    }
    
    // 连接WebSocket
    const connectWebSocket = () => {
      if (wsConnection.value && wsConnection.value.readyState !== WebSocket.CLOSED) {
        wsConnection.value.close()
      }
      
      wsStatus.value = '连接中...'
      addLog('正在连接WebSocket...', 'info')
      const protocol = window.location.protocol === 'https:' ? 'wss:' : 'ws:'
      const wsUrl = `${protocol}//${window.location.host}/captcha/ws/${sessionId.value}`
      
      addLog(`WebSocket连接URL: ${wsUrl}`, 'info')
      
      try {
        const ws = new WebSocket(wsUrl)
        
        ws.onopen = () => {
          addLog('WebSocket连接已建立', 'success')
          wsStatus.value = '已连接'
          
          // 连接成功后主动请求一次截图
          ws.send("get_screenshot")
        }
        
        ws.onmessage = (event) => {
          try {
            const data = JSON.parse(event.data)
            lastMessage.value = JSON.stringify(data).substring(0, 50) + '...'
            
            if (data.type === 'screenshot_update' && data.screenshot) {
              screenshot.value = `data:image/png;base64,${data.screenshot}`
              loading.value = false
              lastUpdateTime.value = new Date().toLocaleTimeString()
              addLog('收到新的截图', 'info')
            } else if (data.type === 'status_update') {
              sessionStatus.value = data.status
              addLog(`收到状态更新: ${data.status}`, 'info')
              
              if (data.status === 'completed') {
                sessionCompleted.value = true
                ElMessage.success('验证已完成')
              }
            } else if (data.type === 'error') {
              addLog(`收到错误消息: ${data.message}`, 'error')
              ElMessage.error(data.message)
            }
          } catch (error) {
            addLog(`解析WebSocket消息失败: ${error.message}`, 'error')
          }
        }
        
        ws.onerror = (error) => {
          addLog(`WebSocket错误: ${error}`, 'error')
          wsStatus.value = '连接错误'
        }
        
        ws.onclose = () => {
          addLog('WebSocket连接已关闭', 'warning')
          wsStatus.value = '已关闭'
          
          // 如果不是因为会话完成而关闭的，则尝试重连
          if (!sessionCompleted.value) {
            setTimeout(connectWebSocket, 3000)
          }
        }
        
        wsConnection.value = ws
      } catch (error) {
        addLog(`创建WebSocket连接失败: ${error.message}`, 'error')
        wsStatus.value = '创建连接失败'
      }
    }
    
    // 重新连接WebSocket
    const reconnectWebSocket = () => {
      wsStatus.value = '正在重连...'
      connectWebSocket()
    }
    
    // 获取会话信息
    const fetchSessionInfo = async () => {
      try {
        const response = await axios.get(`/captcha/api/check_verification/${sessionId.value}`)
        console.log('会话信息:', response.data)
        const { verified, status } = response.data
        
        sessionStatus.value = status || (verified ? 'completed' : 'active')
        sessionCompleted.value = verified
        
        if (verified) {
          if (wsConnection.value) {
            wsConnection.value.close()
          }
        }
      } catch (error) {
        console.error('获取会话信息失败:', error)
      }
    }
    
    // 加载直接截图
    const loadDirectScreenshot = async () => {
      try {
        addLog('等待通过WebSocket接收截图...', 'info');
        // 不再尝试直接加载截图URL，而是通过WebSocket更新
        // 如果WebSocket还未连接，这里触发连接
        if (!wsConnection.value || wsConnection.value.readyState !== WebSocket.OPEN) {
          connectWebSocket();
        } else {
          // 如果已经连接，则发送请求获取截图
          wsConnection.value.send('get_screenshot');
        }
        
        // 设置超时
        const timeout = setTimeout(() => {
          if (loading.value) {
            addLog('截图加载超时，请刷新页面或重新连接WebSocket', 'warning');
          }
        }, 5000);
        
        return () => clearTimeout(timeout);
      } catch (error) {
        console.error('加载截图失败:', error);
        addLog('截图加载失败，请点击"刷新页面"按钮', 'error');
      }
    }
    
    // 添加日志条目
    const addLog = (message, type = 'info') => {
      const time = new Date().toLocaleTimeString()
      operationLogs.value.unshift({ time, message, type })
      
      // 限制日志数量
      if (operationLogs.value.length > maxLogs) {
        operationLogs.value = operationLogs.value.slice(0, maxLogs)
      }
      
      // 特定操作产生智能提示
      if (type === 'error') {
        suggestAction('遇到错误，您可以尝试刷新页面或切换操作模式')
      }
    }
    
    // 清空日志
    const clearLogs = () => {
      operationLogs.value = []
    }
    
    // 处理屏幕点击
    const handleScreenClick = async (event) => {
      if (loading.value || sessionCompleted.value) return
      
      const rect = browserOverlay.value.getBoundingClientRect()
      const x = event.clientX - rect.left
      const y = event.clientY - rect.top
      
      // 如果是拖动模式
      if (isDragMode.value === true) {
        // 记录拖动起点
        dragStart.x = x
        dragStart.y = y
        
        // 提示用户
        addLog(`已选择滑块起点: (${Math.round(x)}, ${Math.round(y)})`, 'info')
        ElMessage.info('已选择起点，请再次点击设置终点')
        
        // 切换到终点选择
        isDragMode.value = 'endpoint'
        return
      }
      
      // 如果是终点选择
      if (isDragMode.value === 'endpoint') {
        // 记录拖动终点
        dragEnd.x = x
        dragEnd.y = y
        
        addLog(`已选择目标点: (${Math.round(x)}, ${Math.round(y)})`, 'info')
        
        // 执行拖动
        await executeDrag()
        
        // 退出拖动模式
        isDragMode.value = false
        return
      }
      
      // 显示点击指示器
      indicatorPosition.x = x
      indicatorPosition.y = y
      showClickIndicator.value = true
      
      setTimeout(() => {
        showClickIndicator.value = false
      }, 500)
      
      loading.value = true
      addLog(`执行点击: (${Math.round(x)}, ${Math.round(y)})`, 'info')
      
      try {
        console.log('执行点击操作:', x, y)
        const response = await axios.post(`/captcha/api/session/${sessionId.value}/action`, {
          action_type: 'click',
          x: Math.round(x),
          y: Math.round(y)
        })
        
        // 处理响应
        if (response.data.success) {
          addLog(`点击成功: ${response.data.details?.method ? '方法' + response.data.details.method : ''}`, 'success')
        } else {
          addLog(`点击失败: ${response.data.details?.error || '未知错误'}`, 'error')
        }
      } catch (error) {
        console.error('执行点击操作失败:', error)
        addLog(`点击异常: ${error.message || '未知错误'}`, 'error')
      } finally {
        loading.value = false
      }
    }
    
    // 处理鼠标移动
    const handleMouseMove = (event) => {
      if (loading.value || sessionCompleted.value) return
      
      const rect = browserOverlay.value.getBoundingClientRect()
      mousePosition.x = event.clientX - rect.left
      mousePosition.y = event.clientY - rect.top
      
      // 如果鼠标按下状态，且处于拖动模式，可以实时预览拖动效果
      if (isMouseDown.value && interactionMode.value === 'drag') {
        // 可以添加拖动预览的逻辑
      }
    }
    
    // 处理鼠标按下
    const handleMouseDown = (event) => {
      if (loading.value || sessionCompleted.value) return
      
      isMouseDown.value = true
      
      const rect = browserOverlay.value.getBoundingClientRect()
      const x = event.clientX - rect.left
      const y = event.clientY - rect.top
      
      if (interactionMode.value === 'drag') {
        dragStart.x = x
        dragStart.y = y
        addLog(`开始拖动，起点: (${Math.round(x)}, ${Math.round(y)})`, 'info')
      }
    }
    
    // 处理鼠标释放
    const handleMouseUp = async (event) => {
      if (loading.value || sessionCompleted.value) return
      
      if (!isMouseDown.value) return
      isMouseDown.value = false
      
      const rect = browserOverlay.value.getBoundingClientRect()
      const x = event.clientX - rect.left
      const y = event.clientY - rect.top
      
      // 如果是拖动模式，且起点和终点不同，执行拖动
      if (interactionMode.value === 'drag') {
        // 判断是否移动了足够的距离
        const deltaX = x - dragStart.x
        const deltaY = y - dragStart.y
        
        if (Math.abs(deltaX) > 10 || Math.abs(deltaY) > 10) {
          dragEnd.x = x
          dragEnd.y = y
          addLog(`结束拖动，终点: (${Math.round(x)}, ${Math.round(y)})`, 'info')
          await executeDrag()
        }
      }
    }
    
    // 设置交互模式
    const setInteractionMode = (mode) => {
      interactionMode.value = mode
      isDragMode.value = (mode === 'drag')
      
      if (mode === 'drag') {
        addLog('已切换到拖动模式：按住鼠标拖动滑块', 'info')
        suggestAction('请在滑块上按下鼠标并拖动到目标位置')
      } else {
        addLog('已切换到点击模式', 'info')
        suggestAction('点击模式已激活，点击验证码中需要选择的元素')
      }
    }
    
    // 放大查看
    const zoomIn = () => {
      addLog('放大功能暂未实现', 'info')
      suggestAction('您可以使用浏览器自带的缩放功能进行放大 (Ctrl + +)')
    }
    
    // 提供智能建议
    const suggestAction = (suggestion) => {
      aiSuggestion.value = suggestion
      // 5秒后自动清除提示
      setTimeout(() => {
        if (aiSuggestion.value === suggestion) {
          aiSuggestion.value = ''
        }
      }, 5000)
    }
    
    // 执行拖动
    const executeDrag = async () => {
      loading.value = true
      addLog(`执行拖动: 从(${Math.round(dragStart.x)}, ${Math.round(dragStart.y)})到(${Math.round(dragEnd.x)}, ${Math.round(dragEnd.y)})`, 'info')
      
      try {
        console.log(`执行拖动操作: 从(${dragStart.x}, ${dragStart.y})到(${dragEnd.x}, ${dragEnd.y})`)
        const response = await axios.post(`/captcha/api/session/${sessionId.value}/action`, {
          action_type: 'drag',
          x: Math.round(dragStart.x),
          y: Math.round(dragStart.y),
          target_x: Math.round(dragEnd.x)
        })
        
        if (response.data.success) {
          addLog(`拖动成功: ${response.data.details?.method ? '方法' + response.data.details.method : ''}`, 'success')
          ElMessage.success('拖动操作已执行')
        } else {
          addLog(`拖动失败: ${response.data.details?.error || '未知错误'}`, 'error')
          ElMessage.error('拖动操作失败')
        }
      } catch (error) {
        console.error('执行拖动操作失败:', error)
        addLog(`拖动异常: ${error.message || '未知错误'}`, 'error')
        ElMessage.error('拖动操作失败')
      } finally {
        loading.value = false
      }
    }
    
    // 初始化拖动模式
    const initDragMode = () => {
      isDragMode.value = true
      addLog('进入滑块拖动模式，请先点击滑块位置', 'info')
      suggestAction('请先点击滑块的起始位置，然后再点击需要拖动到的目标位置')
    }
    
    // 刷新浏览器
    const refreshBrowser = async () => {
      if (loading.value || sessionCompleted.value) return
      
      loading.value = true
      addLog('刷新页面', 'info')
      
      try {
        const response = await axios.post(`/captcha/api/session/${sessionId.value}/action`, {
          action_type: 'refresh'
        })
        
        if (response.data.success) {
          addLog('页面刷新成功', 'success')
        } else {
          addLog(`页面刷新失败: ${response.data.details?.error || '未知错误'}`, 'error')
        }
        
        console.log('页面已刷新')
      } catch (error) {
        console.error('刷新页面失败:', error)
        addLog(`刷新页面异常: ${error.message || '未知错误'}`, 'error')
      } finally {
        loading.value = false
      }
    }
    
    // 完成验证
    const completeVerification = async () => {
      if (loading.value || sessionCompleted.value) return
      
      loading.value = true
      addLog('标记验证完成', 'info')
      
      try {
        const response = await axios.post(`/captcha/api/session/${sessionId.value}/action`, {
          action_type: 'complete'
        })
        
        sessionCompleted.value = true
        sessionStatus.value = 'completed'
        
        if (response.data.success) {
          addLog('验证已标记为完成', 'success')
        }
        
        console.log('验证已完成')
        
        if (wsConnection.value) {
          wsConnection.value.close()
        }
      } catch (error) {
        console.error('完成验证失败:', error)
        addLog(`标记验证完成异常: ${error.message || '未知错误'}`, 'error')
      } finally {
        loading.value = false
      }
    }
    
    // 关闭会话
    const closeSession = async () => {
      if (loading.value) return
      
      loading.value = true
      
      try {
        await axios.post(`/captcha/api/session/${sessionId.value}/action`, {
          action_type: 'close'
        })
        
        sessionStatus.value = 'closed'
        
        console.log('会话已关闭')
        
        if (wsConnection.value) {
          wsConnection.value.close()
        }
        
        // 返回列表页
        setTimeout(() => {
          router.push('/captcha')
        }, 1500)
      } catch (error) {
        console.error('关闭会话失败:', error)
      } finally {
        loading.value = false
      }
    }
    
    // 返回列表
    const backToList = () => {
      router.push('/captcha')
    }
    
    // 组件挂载
    onMounted(() => {
      console.log('组件已挂载')
      addLog('页面已加载，正在获取验证会话信息', 'info')
      fetchSessionInfo()
      connectWebSocket()
      // 尝试直接加载截图
      loadDirectScreenshot()
      
      // 智能分析验证码类型
      setTimeout(() => {
        if (screenshot.value) {
          analyzeScreenshot()
        }
      }, 2000)
      
      // 每10秒检查会话状态
      const intervalId = setInterval(fetchSessionInfo, 10000)
      
      // 添加键盘快捷键
      const handleKeyDown = (event) => {
        if (loading.value || sessionCompleted.value) return
        
        // 按下Esc取消当前模式
        if (event.key === 'Escape') {
          if (isDragMode.value) {
            isDragMode.value = false
            interactionMode.value = 'click'
            addLog('已取消拖动模式', 'info')
          }
          aiSuggestion.value = ''
          return
        }
        
        // 功能键
        if (event.key === 'F1') {
          // F1显示/隐藏快捷键
          showShortcuts.value = !showShortcuts.value
          return
        }
        
        if (event.key === 'F2' || event.key === 'd' || event.key === 'D') {
          // F2或D显示/隐藏调试信息
          showDebug.value = !showDebug.value
          return
        }
        
        // 其他快捷键
        switch (event.key.toLowerCase()) {
          case 'r':
            // R键刷新页面
            refreshBrowser()
            break
          case 'd':
            // D键切换拖动模式
            initDragMode()
            break
          case 'c':
            // C键完成验证
            completeVerification()
            break
          case 't':
            // T键切换工具栏显示
            isToolbarVisible.value = !isToolbarVisible.value
            break
        }
      }
      
      window.addEventListener('keydown', handleKeyDown)
      
      // 智能分析截图，识别验证码类型并提供建议
      const analyzeScreenshot = () => {
        // 这里可以添加更复杂的验证码类型识别逻辑
        suggestAction('系统已准备就绪，请操作验证码。如需帮助请按F1查看快捷键')
      }
      
      onBeforeUnmount(() => {
        clearInterval(intervalId)
        if (wsConnection.value) {
          wsConnection.value.close()
        }
        window.removeEventListener('keydown', handleKeyDown)
      })
    })
    
    return {
      // 数据
      sessionId,
      sessionStatus,
      sessionCreatedAt,
      sessionCompleted,
      screenshot,
      loading,
      showClickIndicator,
      indicatorStyle,
      wsStatus,
      lastMessage,
      lastUpdateTime,
      isDragMode,
      showDebug,
      operationLogs,
      interactionMode,
      isToolbarVisible,
      toolbarPosition,
      showShortcuts,
      aiSuggestion,
      
      // 方法
      formatTime,
      handleScreenClick,
      refreshBrowser,
      completeVerification,
      closeSession,
      backToList,
      reconnectWebSocket,
      initDragMode,
      clearLogs,
      handleMouseMove,
      handleMouseDown,
      handleMouseUp,
      setInteractionMode,
      zoomIn,
      
      // 引用
      browserContainer,
      browserOverlay
    }
  }
}
</script>

