<script>
import { computed } from 'vue';
import { useRoute } from 'vue-router';
import { Home, List, Document, DataAnalysis, Key } from '@element-plus/icons-vue';

export default {
  name: 'App',
  
  components: {
    Home,
    List,
    Document,
    DataAnalysis,
    Key
  },
  
  setup() {
    const route = useRoute();
    
    // 当前活动的菜单项
    const activeIndex = computed(() => {
      return route.path;
    });
    
    // 当前年份
    const currentYear = new Date().getFullYear();
    
    return {
      activeIndex,
      currentYear
    };
  }
};
