<template>
  <div class="house-list">
    <el-card>
      <template #header>
        <div class="card-header">
          <h3>房源列表</h3>
          <div class="filter-actions">
            <el-button type="primary" size="small" @click="refreshData">刷新</el-button>
            <el-button type="success" size="small" @click="exportData">导出数据</el-button>
          </div>
        </div>
      </template>
      
      <!-- 筛选条件 -->
      <div class="filter-container">
        <el-form :model="filters" inline>
          <el-form-item label="城市">
            <el-select v-model="filters.city" placeholder="选择城市" clearable @change="handleCityChange">
              <el-option
                v-for="(code, name) in cities"
                :key="name"
                :label="name"
                :value="name"
              />
            </el-select>
          </el-form-item>
          
          <el-form-item label="区域">
            <el-select v-model="filters.district" placeholder="选择区域" clearable>
              <el-option
                v-for="district in districts"
                :key="district"
                :label="district"
                :value="district"
              />
            </el-select>
          </el-form-item>
          
          <el-form-item label="价格区间">
            <el-input-number v-model="filters.minPrice" placeholder="最低价" :min="0" />
            <span class="separator">-</span>
            <el-input-number v-model="filters.maxPrice" placeholder="最高价" :min="0" />
          </el-form-item>
          
          <el-form-item label="户型">
            <el-select v-model="filters.roomCount" placeholder="房间数" clearable>
              <el-option v-for="i in 5" :key="i" :label="`${i}室`" :value="i" />
            </el-select>
          </el-form-item>
          
          <el-form-item>
            <el-button type="primary" @click="applyFilters">筛选</el-button>
            <el-button @click="resetFilters">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      
      <!-- 房源列表 -->
      <el-table :data="houses" stripe style="width: 100%" v-loading="loading">
        <el-table-column type="expand">
          <template #default="props">
            <div class="house-detail">
              <el-row :gutter="20">
                <el-col :span="8">
                  <img :src="props.row.image" alt="房源图片" class="house-image" />
                </el-col>
                <el-col :span="16">
                  <p><strong>地址:</strong> {{props.row.location_qu}} {{props.row.location_big}} {{props.row.location_small}}</p>
                  <p><strong>户型:</strong> {{props.row.room}}</p>
                  <p><strong>面积:</strong> {{props.row.size}}㎡</p>
                  <p><strong>单价:</strong> {{props.row.unit_price}}元/㎡/月</p>
                  <p><strong>朝向:</strong> {{props.row.direction}}</p>
                  <p><strong>楼层:</strong> {{props.row.floor}}</p>
                  <p><strong>爬取时间:</strong> {{formatDate(props.row.crawl_time)}}</p>
                  <el-button type="primary" size="small" @click="openLink(props.row.link)">查看详情</el-button>
                </el-col>
              </el-row>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="title" label="标题" min-width="250" />
        <el-table-column prop="price" label="价格(元/月)" width="120" sortable />
        <el-table-column prop="location_qu" label="区域" width="100" />
        <el-table-column prop="size" label="面积(㎡)" width="100" sortable />
        <el-table-column prop="room" label="户型" width="120" />
        <el-table-column prop="direction" label="朝向" width="100" />
      </el-table>
      
      <!-- 分页 -->
      <div class="pagination-container">
        <el-pagination
          background
          layout="total, sizes, prev, pager, next"
          :total="total"
          :page-size="pageSize"
          :current-page="currentPage"
          :page-sizes="[10, 20, 50, 100]"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </el-card>
  </div>
</template>

<script>
import { ref, reactive, onMounted, watch } from 'vue';
import { ElMessage } from 'element-plus';
import { useRoute } from 'vue-router';
import api from '../api/api';

export default {
  name: 'HouseList',
  
  setup() {
    const route = useRoute();
    
    // 房源数据
    const houses = ref([]);
    const loading = ref(false);
    
    // 筛选条件
    const filters = reactive({
      city: '',
      district: '',
      minPrice: null,
      maxPrice: null,
      roomCount: null
    });
    
    // 城市和区域数据
    const cities = ref({});
    const districts = ref([]);
    
    // 分页参数
    const currentPage = ref(1);
    const pageSize = ref(20);
    const total = ref(0);
    
    // 获取房源数据
    const fetchData = async () => {
      try {
        loading.value = true;
        
        // 构建请求参数
        const params = {
          city: filters.city || undefined,
          district: filters.district || undefined,
          min_price: filters.minPrice || undefined,
          max_price: filters.maxPrice || undefined,
          room_count: filters.roomCount || undefined,
          limit: pageSize.value,
          offset: (currentPage.value - 1) * pageSize.value
        };
        
        // 获取房源列表
        houses.value = await api.getHouses(params);
        
        // 获取总数量
        const countResult = await api.getHouseCount(params);
        total.value = countResult.count;
        
        loading.value = false;
      } catch (error) {
        loading.value = false;
        ElMessage.error('获取房源数据失败');
        console.error(error);
      }
    };
    
    // 获取城市列表
    const getCities = async () => {
      try {
        cities.value = await api.getCities();
      } catch (error) {
        ElMessage.error('获取城市列表失败');
        console.error(error);
      }
    };
    
    // 根据城市获取区域列表
    const getDistricts = async (city) => {
      try {
        if (!city) {
          districts.value = [];
          return;
        }
        
        districts.value = await api.getDistricts(city);
      } catch (error) {
        ElMessage.error('获取区域列表失败');
        console.error(error);
      }
    };
    
    // 监听路由参数变化
    watch(() => route.query, (query) => {
      if (query.city) {
        filters.city = query.city;
      }
      if (query.taskId) {
        // 如果指定了任务ID，则只查询该任务的数据
        // 这里需要后端API支持按任务ID筛选
        fetchData();
      }
    }, { immediate: true });
    
    // 城市变化时更新区域列表
    const handleCityChange = (city) => {
      filters.district = '';
      getDistricts(city);
    };
    
    // 刷新数据
    const refreshData = () => {
      fetchData();
    };
    
    // 应用筛选条件
    const applyFilters = () => {
      currentPage.value = 1;
      fetchData();
    };
    
    // 重置筛选条件
    const resetFilters = () => {
      // 保留城市筛选，重置其他条件
      const city = filters.city;
      
      Object.assign(filters, {
        city,
        district: '',
        minPrice: null,
        maxPrice: null,
        roomCount: null
      });
      
      currentPage.value = 1;
      fetchData();
    };
    
    // 导出数据
    const exportData = () => {
      ElMessage.info('导出功能开发中');
      // TODO: 实现导出功能，可能需要后端支持
    };
    
    // 在新标签页打开链接
    const openLink = (url) => {
      window.open(url, '_blank');
    };
    
    // 处理页码变化
    const handleCurrentChange = (page) => {
      currentPage.value = page;
      fetchData();
    };
    
    // 处理每页显示数量变化
    const handleSizeChange = (size) => {
      pageSize.value = size;
      currentPage.value = 1;
      fetchData();
    };
    
    // 格式化日期
    const formatDate = (dateString) => {
      if (!dateString) return '';
      
      const date = new Date(dateString);
      return date.toLocaleString();
    };
    
    // 组件挂载时获取初始数据
    onMounted(() => {
      getCities();
      
      // 如果URL中包含城市参数，则获取对应的区域列表
      if (route.query.city) {
        filters.city = route.query.city;
        getDistricts(filters.city);
      }
      
      fetchData();
    });
    
    return {
      houses,
      loading,
      filters,
      cities,
      districts,
      currentPage,
      pageSize,
      total,
      handleCityChange,
      refreshData,
      applyFilters,
      resetFilters,
      exportData,
      openLink,
      handleCurrentChange,
      handleSizeChange,
      formatDate
    };
  }
};
</script>

<style scoped>
.house-list {
  margin-bottom: 20px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.filter-container {
  margin-bottom: 20px;
}

.filter-actions {
  display: flex;
  gap: 10px;
}

.separator {
  margin: 0 5px;
}

.pagination-container {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.house-detail {
  padding: 10px;
}

.house-image {
  width: 100%;
  max-width: 240px;
  border-radius: 4px;
}
</style> 