 <template>
  <div class="home">
    <el-row :gutter="20">
      <el-col :span="24">
        <div class="welcome-section">
          <h1>租房数据分析系统</h1>
          <p class="subtitle">基于贝壳网的租房数据爬取和分析平台</p>
        </div>
      </el-col>
    </el-row>

    <el-row :gutter="20">
      <el-col :span="16">
        <el-card class="intro-card">
          <template #header>
            <div class="card-header">
              <h2>系统功能介绍</h2>
            </div>
          </template>
          <div class="system-intro">
            <p>本系统是一个基于Python技术栈开发的租房数据分析平台，通过爬取贝壳网的租房信息，并使用PySpark进行数据处理和分析，最终以可视化图表的形式展示市场洞察。</p>
            
            <h3>主要功能:</h3>
            <el-row :gutter="20">
              <el-col :span="8">
                <el-card shadow="hover" class="feature-card">
                  <template #header>
                    <div class="feature-header">
                      <el-icon><Download /></el-icon>
                      <span>数据采集</span>
                    </div>
                  </template>
                  <p>支持多城市租房数据爬取，自动解析房源价格、面积、位置等关键信息</p>
                </el-card>
              </el-col>
              
              <el-col :span="8">
                <el-card shadow="hover" class="feature-card">
                  <template #header>
                    <div class="feature-header">
                      <el-icon><DataAnalysis /></el-icon>
                      <span>数据分析</span>
                    </div>
                  </template>
                  <p>使用PySpark对租房数据进行多维度分析，包括区域分析、户型分析、价格分布等</p>
                </el-card>
              </el-col>
              
              <el-col :span="8">
                <el-card shadow="hover" class="feature-card">
                  <template #header>
                    <div class="feature-header">
                      <el-icon><PieChart /></el-icon>
                      <span>可视化展示</span>
                    </div>
                  </template>
                  <p>通过ECharts图表直观展示分析结果，帮助用户快速了解租房市场情况</p>
                </el-card>
              </el-col>
            </el-row>
            
            <h3>快速开始:</h3>
            <ol>
              <li>在右侧创建新的爬虫任务，选择城市和爬取页数</li>
              <li>等待爬虫任务完成后，在<router-link to="/tasks">任务列表</router-link>中查看详情</li>
              <li>点击"查看数据"浏览爬取的房源信息，或点击"分析数据"进行数据分析</li>
              <li>在<router-link to="/analysis">数据分析</router-link>页面查看生成的可视化图表</li>
            </ol>
          </div>
        </el-card>
      </el-col>
      
      <el-col :span="8">
        <CrawlTaskForm @task-created="handleTaskCreated" />
        
        <el-card class="latest-tasks" v-loading="loading">
          <template #header>
            <div class="card-header">
              <h3>最近爬虫任务</h3>
              <el-button type="primary" link @click="goToTaskList">查看全部</el-button>
            </div>
          </template>
          
          <div v-if="recentTasks.length > 0">
            <div v-for="task in recentTasks" :key="task.id" class="task-item">
              <div class="task-info">
                <h4>{{ task.city }}</h4>
                <p>
                  状态: <el-tag :type="getStatusType(task.status)" size="small">{{ task.status }}</el-tag>
                </p>
                <p>开始时间: {{ formatDate(task.start_time) }}</p>
                <p>房源数: {{ task.success_items }}/{{ task.total_items }}</p>
              </div>
              <div class="task-actions">
                <el-button 
                  type="primary" 
                  size="small" 
                  @click="viewTaskDetail(task)"
                  :disabled="task.status === '进行中'"
                >
                  查看
                </el-button>
              </div>
            </div>
          </div>
          <el-empty v-else description="暂无爬虫任务" />
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue';
import { useRouter } from 'vue-router';
import { ElMessage } from 'element-plus';
import CrawlTaskForm from '../components/CrawlTaskForm.vue';
import api from '../api/api';

export default {
  name: 'HomeView',
  
  components: {
    CrawlTaskForm
  },
  
  setup() {
    const router = useRouter();
    const loading = ref(false);
    const recentTasks = ref([]);
    
    // 获取最近任务
    const fetchRecentTasks = async () => {
      try {
        loading.value = true;
        
        const params = {
          limit: 5,
          offset: 0
        };
        
        recentTasks.value = await api.getTasks(params);
        loading.value = false;
      } catch (error) {
        loading.value = false;
        ElMessage.error('获取最近任务失败');
        console.error(error);
      }
    };
    
    // 处理任务创建成功事件
    const handleTaskCreated = (task) => {
      ElMessage.success(`成功创建爬虫任务：${task.city}`);
      fetchRecentTasks();
    };
    
    // 跳转到任务列表页
    const goToTaskList = () => {
      router.push({ name: 'taskList' });
    };
    
    // 查看任务详情
    const viewTaskDetail = (task) => {
      router.push({ 
        name: 'houseList', 
        query: { 
          taskId: task.id,
          city: task.city
        } 
      });
    };
    
    // 获取状态对应的类型
    const getStatusType = (status) => {
      switch (status) {
        case '进行中':
        case 'In Progress':
          return 'warning';
        case '完成':
        case 'Completed':
          return 'success';
        case '失败':
        case 'Failed':
          return 'danger';
        default:
          return 'info';
      }
    };
    
    // 格式化日期
    const formatDate = (dateString) => {
      if (!dateString) return '';
      
      const date = new Date(dateString);
      return date.toLocaleString();
    };
    
    // 组件挂载时获取最近任务
    onMounted(() => {
      fetchRecentTasks();
    });
    
    return {
      loading,
      recentTasks,
      handleTaskCreated,
      goToTaskList,
      viewTaskDetail,
      getStatusType,
      formatDate
    };
  }
};
</script>

<style scoped>
.home {
  padding: 0;
}

.welcome-section {
  text-align: center;
  margin: 20px 0 30px;
}

.welcome-section h1 {
  font-size: 2.5rem;
  margin-bottom: 10px;
  color: #409EFF;
}

.subtitle {
  font-size: 1.2rem;
  color: #606266;
  margin-bottom: 20px;
}

.intro-card {
  margin-bottom: 20px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card-header h2, .card-header h3 {
  margin: 0;
}

.system-intro {
  line-height: 1.6;
}

.system-intro h3 {
  margin-top: 20px;
  margin-bottom: 15px;
}

.feature-card {
  height: 100%;
  margin-bottom: 20px;
}

.feature-header {
  display: flex;
  align-items: center;
  gap: 8px;
}

.latest-tasks {
  margin-top: 20px;
}

.task-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid #EBEEF5;
}

.task-item:last-child {
  border-bottom: none;
}

.task-info h4 {
  margin: 0 0 5px 0;
}

.task-info p {
  margin: 5px 0;
  font-size: 0.9rem;
  color: #606266;
}

.task-actions {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
</style>